import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      component: () => import("@/layouts/default"),
      children: [
        { path: "", component: () => import("@/pages/default") },
        { path: "pageun", component: () => import("@/pages/pageun") },
        { path: "pagedeux", component: () => import("@/pages/pagedeux") },
        { path: "pagetrois", component: () => import("@/pages/pagetrois") }
      ]
    }
  ]
});



