import Vue from "vue";
import Vuex from "vuex";

import global from "./global";
import foos from "./foos";
import layout from "./layout";

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    global,
    foos,
    layout
  }
});

export default store;



